.filter-dropdown {
  position: relative;
  display: inline-block;
  width: 15vw;
  min-width: 120px;
  border-radius: 10px;
}

.dropdown-toggle {
  width: 100%;
  min-width: 60px;
  height: 35px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: end;
  font-weight: 600;
  color: var(--color-blue);
  gap: 10px;
  background-color: var(--color-white);
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.dropdown-menu {
  position: absolute;
  width: 100%;
  display: flex;
  flex-direction: column;
  height: fit-content;
  gap: 10px;
  padding: 16px 8px 12px;
  background-color: var(--color-white);
  z-index: 1000;
  border-radius: 8px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.dropdown-menu label {
  cursor: pointer;
  padding-left: 10px;
}

.filterActive {
  border-radius: 8px 8px 0 0;
  /* width: 16vw; */
  padding-right: 20px;
  display: flex;
  flex-direction: row;
  gap: 80px;
}

.apply-button {
  height: 2.5rem;
  width: 80%;
  background-color: var(--color-red);
  color: var(--color-white);
  margin: 5px auto;
  border: none;
  border-radius: 40px;
  cursor: pointer;
}
