.navbar {
  background: linear-gradient(to right, var(--color-blue), var(--color-red));
  color: var(--color-light-gray);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  height: 15vh;
  max-height: 96px;
  width: 100%;
  z-index: 1000;
}

.navbar-logo {
    margin-left: 3vw;
}

.navbar-items {
  display: flex;
  flex-direction: row;
  gap: 3vw;
  align-items: center;
  justify-content: space-between;
  margin-right: 8vw;
}

.navbar-help-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    gap: 8px;
}
