.filter-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  padding: 7px 0;
  background-color: var(--color-white);
  border-radius: 8px;
}

.filter-button {
  width: 15vw;
  min-width: 100px;
  height: 2rem;
  border: none;
  cursor: pointer;
  border-radius: 16px;
  background-color: var(--color-white);
  transition: background 0.3s, border-radius 0.3s;
  color: var(--color-dark-gray);
}

.selected {
  background-color: var(--color-light-gray);
}
